import React, { useState } from "react";  
import Square from "./Square";  
import './index.css';     
 
const Panel = () => {
  const revert_Panel = [" ", " ", " ", " ", " ", " ", " ", " ", " "];
  const [amount, switch_amount] = useState(revert_Panel);
  const [bend, shift_bend] = useState(true);
  const [disable, switch_disable] = useState(false); 
  const [msg, set_msg] = useState(""); 

  const win = (i) => {

    let kind = bend ? "T" : "O";   
    
    // vertical analysis 

    if 
    (
      amount[i] === kind && amount[(i + 3) % 9] === kind && amount[(i + 6) % 9] === kind
    ) 
    {
      return true; 
    } 

    // horizontal analysis

    let subs = i > 2 ? 3 : 0;
    subs = i > 5 ? 6 : subs;

    if 
    (
      amount[(i % 3) + subs] === kind && amount[((i + 1) % 3) + subs] === kind &&
      amount[((i + 2) % 3) + subs] === kind
    ) 
    {
      return true;
    } 

    // diagonal analysis

    if (amount[0] === kind && amount[4] === kind && amount[8] === kind) 
    {
      return true;
    }
    if (amount[2] === kind && amount[4] === kind && amount[6] === kind) 
    {
      return true;
    }
    return false;
  }; 

  const onClickSquare = (i) => { 
    if (disable) 
    {
      return;
    } 

    let info = amount;

    if (bend && amount[i] === " ") 
    {
      info[i] = "T";
    } 
    else if (amount[i] === " ") 
    {
      info[i] = "O";
    } 
    else 
    {
      return;
    }

    switch_amount(info);
    shift_bend(!bend);

    if (!amount.includes(" ")) 
    {
      set_msg(`Game is Tie`);
    } 

    if (win(i)) 
    {
      switch_disable(true);
      set_msg(`${bend ? "T" : "O"} Wins`);
    }
  };

  const reset_controller = () => 
  {
    switch_disable(false);
    switch_amount(revert_Panel);
    set_msg("");
  };

  return (
    <div className="Panel">
      {amount.map((val, i) => (
        <span
          key={i}
          onClick={() => {
             onClickSquare(i); 
          }}
        >
          <Square character={val} />
        </span>
      ))}
      <h1 className={!bend ? "oneSignal Message" : "secSignal Message"}>
        {msg} 
      </h1>
      <button onClick={reset_controller} className="btn reset-btn mt-10"> 
        Reset
      </button>
    </div>    
  );    
};
  
export default Panel;         